<template name="setting">
	<view>
		<cu-custom bgColor="bg-gradual-red">
			<block slot="content">设置</block>
		</cu-custom>
		<!-- 	
		<view class="cu-bar bg-white shadow shadow-blur radius-bottom" :style="{position: 'fixed', left: 0, width: '100%', top:CustomBar + 'px', zIndex: 1000}">
			<view class="action">
				<button v-if="!isSettingLight" class="cu-btn round lines-red" @click="Delete"><text>删除</text></button>
				<button v-else class="cu-btn round lines-black sm" @click="LightBack"><text class="text-sm">返回</text></button>
			</view>
			<view class="content text-bold"></view>
			<view class="action">
				<button v-if="!isSettingLight" @click="Mute" class="cu-btn round margin-right-xs" :class="isMuted?'bg-red':'lines-black'"><text>到点循环</text></button>
				<button v-if="!isSettingLight" @click="All" class="cu-btn round margin-right-xs" :class="isAll?'bg-red':'lines-black'"><text>循环</text></button>
				<button v-if="!isSettingLight" @click="Single" class="cu-btn round margin-right-xs" :class="isSingle?'bg-red':'lines-black'"><text>单曲</text></button>
				<button v-if="isSettingLight" id="MODE01" @click="SelectLight" :class="lightMode=='MODE01'?'bg-red':'lines-black'"
				 class="cu-btn round sm margin-right-xs"><text class="text-xs">模式一</text></button>
				<button v-if="isSettingLight" id="MODE02" @click="SelectLight" :class="lightMode=='MODE02'?'bg-red':'lines-black'"
				 class="cu-btn round sm margin-right-xs"><text class="text-xs">模式二</text></button>
				<button v-if="isSettingLight" id="MODE03" @click="SelectLight" :class="lightMode=='MODE03'?'bg-red':'lines-black'"
				 class="cu-btn round sm margin-right-xs"><text class="text-xs">模式三</text></button>
				<button v-if="isSettingLight" id="MODE04" @click="SelectLight" :class="lightMode=='MODE04'?'bg-red':'lines-black'"
				 class="cu-btn round sm margin-right-xs"><text class="text-xs">模式四</text></button>
				<button v-if="!isSettingLight" @click="PlayWhenBoot" class="cu-btn round lines-black margin-right-xs"><text class="text-xs">上电播放</text></button>
				<button v-if="!isSettingLight" @click="LightMode" class="cu-btn round lines-black"><text>灯</text></button>
				<button v-if="isSettingLight" @click="UploadLightMode" class="cu-btn round lines-black sm"><text class="text-sm">确定</text></button>
			</view>
		</view>
	-->

		<!-- 上部操作栏 -->
		<view class="cu-bar">
			<view class="cu-list grid col-5">
				<view class="cu-item" @click="Delete">
					<text>删除</text>
				</view>
				<view class="cu-item" @click="ShutdownBoot">
					<text>开关机</text>
				</view>
				<view class="cu-item" @click="TimingCancel">
					<text>定时</text>
				</view>
				<view class="cu-item" @click="PlayWhenBoot">
					<text>上电播放</text>
				</view>
				<view class="cu-item" @click="LightMode">
					<text>灯模式</text>
				</view>
				<view class="cu-item" @click="All">
					<text>循环</text>
				</view>
				<view class="cu-item" @click="Single">
					<text>单曲</text>
				</view>
				<view class="cu-item" @click="Last">
					<text>上一曲</text>
				</view>
				<view class="cu-item" @click="Next">
					<text>下一曲</text>
				</view>
				<view class="cu-item" @click="Stop">
					<text>停止</text>
				</view>
				<view class="cu-item" @click="Mute">
					<text>到点循环</text>
				</view>
				<view class="cu-item" @click="VolumeIncrease">
					<text>音量加</text>
				</view>
				<view class="cu-item" @click="VolumeDecrease">
					<text>音量减</text>
				</view>
			</view>
		</view>
		<!-- 灯模式模态框 -->
		<view class="cu-modal" :class="isSettingLight?'show':''">
			<view class="cu-dialog">
				<view class="cu-bar bg-white justify-end">
					<view class="content">选择灯模式</view>
				</view>
				<radio-group class="block" @change="ChangeLightMode">
					<view class="cu-list menu text-left">
						<view class="cu-item">
							<label class="flex justify-between align-center flex-sub">
								<view class="flex-sub">模式一</view>
								<radio class="round" :class="lightMode=='MODE01'?'checked':''" :value="'MODE01'" :checked="lightMode=='MODE01'?true:false"></radio>
							</label>
						</view>
						<view class="cu-item">
							<label class="flex justify-between align-center flex-sub">
								<view class="flex-sub">模式二</view>
								<radio class="round" :class="lightMode=='MODE02'?'checked':''" :value="'MODE02'" :checked="lightMode=='MODE02'?true:false"></radio>
							</label>
						</view>
						<view class="cu-item">
							<label class="flex justify-between align-center flex-sub">
								<view class="flex-sub">模式三</view>
								<radio class="round" :class="lightMode=='MODE03'?'checked':''" :value="'MODE03'" :checked="lightMode=='MODE03'?true:false"></radio>
							</label>
						</view>
						<view class="cu-item">
							<label class="flex justify-between align-center flex-sub">
								<view class="flex-sub">模式四</view>
								<radio class="round" :class="lightMode=='MODE04'?'checked':''" :value="'MODE04'" :checked="lightMode=='MODE04'?true:false"></radio>
							</label>
						</view>
					</view>
				</radio-group>
				<view class="cu-bar bg-white justify-end">
					<view class="action">
						<button class="cu-btn line-red text-red" @tap="LightBack">取消</button>
						<button class="cu-btn bg-green margin-left" @tap="UploadLightMode">确定</button>
					</view>
				</view>
			</view>
		</view>

		<!-- 文件列表 -->
		<view class="box" style="margin-bottom: 260rpx; margin-top: 120rpx;">
			<view class="cu-list menu margin-top sm-border">
				<view class="cu-item" v-for="(file, index) in fileList" :key="index">
					<view class="content">
						<text class="text-lg">{{"文件 " + (index + 1)}}</text>
					</view>
					<view class="action">
						<view class="flex padding align-center">
							<button class="cu-btn sm round bg-red margin-right-sm" :id="index" @click="Play">试听</button>
							<radio :id="index" class="checked" :checked="(isSelected - 1)==index?true:false" @click="Select"></radio>
						</view>
					</view>
				</view>
			</view>
		</view>
		
		<!-- 底部操作按钮 -->
		<view class="bottomBox">
			<view class="flex justify-center">
				<button class="cu-btn bg-red shadow-blur round lg" style="width: 240rpx;" @click="DownLoadData">
					<text v-if="isDownloading" class="cuIcon-loading1 cuIconfont-spin"></text><text v-else class="text-sm">读取文件</text>
				</button>
			</view>
		</view>
	</view>
</template>

<script>
	export default {
		name: "setting",
		data() {
			return {
				devices: [], //设备列表
				primaryServiceUUID: '', //主服务UUID
				readUUID: '', //通知UUID
				writeUUID: '', //写入UUID
				isSettingLight: false,
				isDownloading: false,
				isSelected: null,
				fileList: [], //文件列表
				lightMode: 'MODE01',
				valueListen: '', //监听的数据
				isMuted: false,
				isSingle: false,
				isAll: false,
				isPlayWhenBoot: false,
				isTimingCancel: false
			}
		},
		methods: {
			//灯模式返回
			LightBack() {
				this.isSettingLight = false
			},
			//选择灯模式方法
			ChangeLightMode(e) {
				this.lightMode = e.detail.value
				console.log(this.lightMode)
			},
			//下载文件
			DownLoadData() {
				console.log("下载文件")
				uni.showToast({
					title: '请稍后...',
					icon: 'loading',
					duration: 1000,
					mask: true
				})
				if (!this.isDownloading && this.devices.length != 0) {
					this.isDownloading = true
					wx.notifyBLECharacteristicValueChange({
						deviceId: this.devices[0].deviceId,
						serviceId: this.primaryServiceUUID,
						characteristicId: this.readUUID,
						state: true,
						success: (res) => {
							uni.showToast({
								title: "打开监听Notify成功",
								icon: "none"
							})
						},
						fail: (res) => {
							uni.showToast({
								title: "打开监听Nofity失败",
								icon: 'none'
							})
						}
					})
					wx.onBLECharacteristicValueChange((res) => {
						wx.offBLECharacteristicValueChange()
						let u8Arr = new Uint8Array(res.value)
						console.log(u8Arr)
						let testArr = [0x7E, 0x04, 0x20]
						for (let cnt1 = 0; cnt1 < u8Arr.length; cnt1++) {
							for (let cnt2 = 0; cnt2 < 3; cnt2++) {
								if (u8Arr[cnt1 + cnt2] == testArr[cnt2]) {
									if (cnt2 == 2) {
										this.fileList = []
										let fileNum = (u8Arr[cnt1 + cnt2 + 1] << 8) | (u8Arr[cnt1 + cnt2 + 2])
										for (let cnt = 1; cnt <= fileNum; cnt++) {
											this.fileList.push(cnt)
										}
										uni.showToast({
											title: "成功读取" + fileNum + "个文件",
											icon: "none"
										})
									}
								} else {
									break;
								}
							}
						}
						setTimeout(() => {
							if (this.fileList.length == 0) {
								uni.showToast({
									title: "读取失败, 请再试一次",
									icon: "none"
								})
							}
						}, 100)
						console.log('特征值 ' + res.characteristicId + '已更新, ' + '现在是' + this.valueListen)
					})
					setTimeout(() => {
						let numArr = new Array()
						numArr.push(0x7E)
						numArr.push(0x02)
						numArr.push(0x19)
						numArr.push(0xEF)
						let u8Arr = new Uint8Array(numArr)
						//写特征值
						wx.writeBLECharacteristicValue({
							deviceId: this.devices[0].deviceId,
							serviceId: this.primaryServiceUUID,
							characteristicId: this.writeUUID,
							value: u8Arr.buffer,
							success: (res) => {
								console.log("发送成功")
								console.log(u8Arr.buffer)
								uni.showToast({
									title: "发送成功",
									icon: "none"
								})
							},
							fail: () => {
								console.log("发送失败")
								uni.showToast({
									title: "发送失败",
									icon: "none"
								})
							}
						})
					}, 100)
					setTimeout(() => {
						this.isDownloading = false
						wx.notifyBLECharacteristicValueChange({
							deviceId: this.devices[0].deviceId,
							serviceId: this.primaryServiceUUID,
							characteristicId: this.readUUID,
							state: false
						})
						wx.offBLECharacteristicValueChange()
					}, 500)
				}
			},
			//打开灯模式二级菜单
			LightMode() {
				console.log("灯模式")
				this.isSettingLight = true
			},
			//下发灯模式信息
			UploadLightMode() {
				console.log("更新灯模式")
				uni.showToast({
					title: '请稍后...',
					icon: 'loading',
					duration: 1000,
					mask: true
				})
				this.isSettingLight = false
				if (this.devices.length) {
					let numArr = new Array()
					numArr.push(0x7E)
					numArr.push(0x03)
					numArr.push(0x17)
					numArr.push(Number(this.lightMode.substr(5, 1)))
					numArr.push(0xEF)
					let u8Arr = new Uint8Array(numArr)
					wx.writeBLECharacteristicValue({
						deviceId: this.devices[0].deviceId,
						serviceId: this.primaryServiceUUID,
						characteristicId: this.writeUUID,
						value: u8Arr.buffer,
						success: (res) => {
							console.log("发送数据成功")
							console.log(u8Arr.buffer)
							uni.showToast({
								title: "更新灯模式成功",
								icon: "none"
							})
						},
						fail: (res) => {
							console.log("发送数据失败")
							uni.showToast({
								title: "更新灯模式失败",
								icon: "none"
							})
						}
					})
				}
			},
			//到点循环或取消到点循环
			Mute() {
				//如果处于到点循环模式就取消到点循环, 下发取消到点循环指令
				uni.showToast({
					title: '到点循环...',
					icon: 'loading',
					duration: 1000,
					mask: true
				})
				if (this.isMuted) {
					console.log("取消到点循环")
					if (this.devices.length) {
						let numArr = new Array()
						numArr.push(0x7E)
						numArr.push(0x03)
						numArr.push(0x15)
						numArr.push(0x00)
						numArr.push(0xEF)
						let u8Arr = new Uint8Array(numArr)
						wx.writeBLECharacteristicValue({
							deviceId: this.devices[0].deviceId,
							serviceId: this.primaryServiceUUID,
							characteristicId: this.writeUUID,
							value: u8Arr.buffer,
							success: (res) => {
								console.log("发送数据成功")
								console.log(u8Arr.buffer)
								this.isMuted = false
								getApp().globalData.isMuted = this.isMuted
								uni.showToast({
									title: "到点循环成功",
									icon: "none"
								})
							},
							fail: (res) => {
								console.log("发送数据失败")
								uni.showToast({
									title: "到点循环失败",
									icon: "none"
								})
							}
						})
					}
				} else { //下发到点循环指令
					console.log("到点循环")
					if (this.devices.length) {
						let numArr = new Array()
						numArr.push(0x7E)
						numArr.push(0x03)
						numArr.push(0x15)
						numArr.push(0x01)
						numArr.push(0xEF)
						let u8Arr = new Uint8Array(numArr)
						wx.writeBLECharacteristicValue({
							deviceId: this.devices[0].deviceId,
							serviceId: this.primaryServiceUUID,
							characteristicId: this.writeUUID,
							value: u8Arr.buffer,
							success: (res) => {
								console.log("发送数据成功")
								console.log(u8Arr.buffer)
								this.isMuted = true
								getApp().globalData.isMuted = this.isMuted
								uni.showToast({
									title: "到点循环成功",
									icon: "none"
								})
								this.isMuted = true
								getApp().globalData.isMuted = this.isMuted
							},
							fail: (res) => {
								console.log("发送数据失败")
								uni.showToast({
									title: "到点循环失败",
									icon: "none"
								})
							}
						})
					}
				}
			},
			//下发单曲指令
			Single() {
				console.log("单曲")
				uni.showToast({
					title: '单曲',
					icon: 'loading',
					duration: 1000,
					mask: true
				})
				if (this.devices.length) {
					let numArr = new Array()
					numArr.push(0x7E)
					numArr.push(0x02)
					numArr.push(0x21)
					numArr.push(0xEF)
					let u8Arr = new Uint8Array(numArr)
					wx.writeBLECharacteristicValue({
						deviceId: this.devices[0].deviceId,
						serviceId: this.primaryServiceUUID,
						characteristicId: this.writeUUID,
						value: u8Arr.buffer,
						success: (res) => {
							console.log("发送数据成功")
							console.log(u8Arr.buffer)
							uni.showToast({
								title: "设置单曲播放成功",
								icon: "none"
							})
							this.isSingle = true
							this.isAll = false
							getApp().globalData.isSingle = this.isSingle
							getApp().globalData.isAll = this.isAll
						},
						fail: (res) => {
							console.log("发送数据失败")
							uni.showToast({
								title: "设置单曲播放失败",
								icon: "none"
							})
						}
					})
				}
			},
			//下发循环指令
			All() {
				uni.showToast({
					title: '循环...',
					icon: 'loading',
					duration: 1000,
					mask: true
				})
				console.log("循环")
				if (this.devices.length) {
					let numArr = new Array()
					numArr.push(0x7E)
					numArr.push(0x02)
					numArr.push(0x16)
					numArr.push(0xEF)
					let u8Arr = new Uint8Array(numArr)
					// var message = "AT+ALL"
					wx.writeBLECharacteristicValue({
						deviceId: this.devices[0].deviceId,
						serviceId: this.primaryServiceUUID,
						characteristicId: this.writeUUID,
						value: u8Arr.buffer,
						success: (res) => {
							console.log("发送数据成功")
							console.log(u8Arr.buffer)
							uni.showToast({
								title: "设置循环播放成功",
								icon: "none"
							})
							this.isSingle = false
							this.isAll = true
							getApp().globalData.isSingle = this.isSingle
							getApp().globalData.isAll = this.isAll
						},
						fail: (res) => {
							console.log("发送数据失败")
							uni.showToast({
								title: "设置循坏播放失败",
								icon: "none"
							})
						}
					})
				}
			},
			//下发删除指令
			Delete() {
				console.log("删除")
				uni.showToast({
					title: '删除...',
					icon: 'loading',
					duration: 1000,
					mask: true
				})
				if (this.devices.length && this.isSelected) {
					let numArr = new Array()
					numArr.push(0x7E)
					numArr.push(0x03)
					numArr.push(0x14)
					numArr.push(this.isSelected)
					numArr.push(0xEF)
					let u8Arr = new Uint8Array(numArr)
					wx.writeBLECharacteristicValue({
						deviceId: this.devices[0].deviceId,
						serviceId: this.primaryServiceUUID,
						characteristicId: this.writeUUID,
						value: u8Arr.buffer,
						success: (res) => {
							console.log("发送数据成功")
							console.log(u8Arr.buffer)
							this.fileList.splice(this.isSelected - 1, 1)
							this.isSelected = null
							uni.hideToast()
							uni.showToast({
								title: "删除文件成功",
								icon: "none"
							})
						},
						fail: (res) => {
							console.log("发送数据失败 " + res.errMsg)
							uni.showToast({
								title: "删除文件失败",
								icon: "none"
							})
						}
					})
				} else {
					console.log("未选择")
				}
			},
			//选中文件单选框方法
			Select(e) {
				this.isSelected = parseInt(e.currentTarget.id) + 1
				console.log("文件" + this.isSelected + "被选中")
			},
			//试听方法
			Play(e) {
				uni.showToast({
					title: '试听...',
					icon: 'loading',
					duration: 1000,
					mask: true
				})
				if (this.devices.length) {
					let numArr = new Array()
					numArr.push(0x7E)
					numArr.push(0x04)
					numArr.push(0x41)
					numArr.push(((parseInt(e.currentTarget.id) + 1) & 0xFF00) >> 8)
					numArr.push((parseInt(e.currentTarget.id) + 1) & 0x00FF)
					numArr.push(0xEF)
					let u8Arr = new Uint8Array(numArr)
					wx.writeBLECharacteristicValue({
						deviceId: this.devices[0].deviceId,
						serviceId: this.primaryServiceUUID,
						characteristicId: this.writeUUID,
						value: u8Arr.buffer,
						success: (res) => {
							console.log("成功发送试听命令")
							console.log(u8Arr.buffer)
							uni.showToast({
								title: "成功发送试听命令",
								icon: 'none'
							})
						},
						fail: (res) => {
							console.log("发送试听命令失败")
							uni.showToast({
								title: "发送试听命令失败",
								icon: 'none'
							})
						}
					})
				}
			},
			//上电播放方法
			PlayWhenBoot() {
				uni.showToast({
					title: '上电播放...',
					icon: 'loading',
					duration: 1000,
					mask: true
				})
				console.log("上电播放")
				if (this.devices.length) {
					let numArr = new Array()
					numArr.push(0x7E)
					numArr.push(0x02)
					numArr.push(0x22)
					numArr.push(0xEF)
					let u8Arr = new Uint8Array(numArr)
					wx.writeBLECharacteristicValue({
						deviceId: this.devices[0].deviceId,
						serviceId: this.primaryServiceUUID,
						characteristicId: this.writeUUID,
						value: u8Arr.buffer,
						success: (res) => {
							console.log("上电播放发送成功")
							console.log(u8Arr.buffer)
							this.isPlayWhenBoot = true
							getApp().globalData.isPlayWhenBoot = this.isPlayWhenBoot
							uni.showToast({
								title: "上电播放发送成功",
								icon: 'none'
							})
						},
						fail: (res) => {
							console.log("上电播放发送失败")
							uni.showToast({
								title: "上电播放发送失败",
								icon: 'none'
							})
						}
